<template>
  <div>
    <bread :msg1="'商品管理'" :msg2="'分类参数'"></bread>
    <el-card class="box-card" :body-style="{ padding: '20px' }">
      <!-- 顶部通知 -->
      <el-alert title="注意：只允许为第三级分类设置相关参数！" type="warning" show-icon :closable="false" effect="dark"> </el-alert>
      <!-- 级联选择器 -->
      <div class="block" style="margin-top:15px;">
        <span class="demonstration" style="margin-right">选择商品分类：</span>
        <el-cascader v-model="value" :options="options" :props="props" @change="handleChange"> </el-cascader>
      </div>
      <el-button type="primary" style="margin-top: 15px;" :disabled="disabled" @click="dialogVisibleAdd = true">
        添加参数
      </el-button>
      <!-- 标签页 -->
      <el-tabs type="border-card" @tab-click="tabClick" v-model="name">
        <el-tab-pane label="动态参数" name="many">
          <el-table style="width: 100%" border :data="manyData">
            <el-table-column type="expand" width="50px">
              <template slot-scope="scoped">
                <el-tag :key="index" v-for="(tag, index) in scoped.row.attr_vals ? scoped.row.attr_vals.split(',') : []" :closable="true" @close="handleClose(scoped, index)">
                  {{ tag }}
                </el-tag>
                <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scoped)" @blur="handleInputConfirm">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index" width="50px"> </el-table-column>
            <el-table-column label="属性名称" prop="attr_name"> </el-table-column>
            <el-table-column label="操作" width="200px">
              <template slot-scope="datas">
                <el-button type="primary" icon="el-icon-edit" size="small" @click="edit(datas)">
                  修改
                </el-button>
                <el-button type="danger" icon="el-icon-edit" @click="del(datas.row)" size="small">
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label="静态属性" name="only">
          <el-table style="width: 100%" border :data="onlyData">
            <el-table-column type="expand" width="50px">
              <template slot-scope="scoped">
                <el-tag :key="index" v-for="(tag, index) in scoped.row.attr_vals ? scoped.row.attr_vals.split(',') : []" :closable="true" @close="handleClose(scoped, index)">
                  {{ tag }}
                </el-tag>
                <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm(scoped)" @blur="handleInputConfirm">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
              </template>
            </el-table-column>
            <el-table-column label="#" type="index" width="50px"> </el-table-column>
            <el-table-column label="属性名称" prop="attr_name"> </el-table-column>
            <el-table-column label="操作" width="200px">
              <template slot-scope="datas">
                <el-button type="primary" icon="el-icon-edit" size="small" @click="edit(datas)">
                  修改
                </el-button>
                <el-button type="danger" icon="el-icon-edit" @click="del(datas.row)" size="small">
                  删除
                </el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!-- 添加参数对话框 -->
      <el-dialog title="添加参数" :visible.sync="dialogVisibleAdd" width="30%" @close="closeDialog">
        <el-form ref="formAdd" :model="form" label-width="80px" :rules="rules">
          <el-form-item label="参数名称" prop="attr_name">
            <el-input v-model="form.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleAdd = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('formAdd')">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 修改名称对话框 -->
      <el-dialog :title="'修改' + title" :visible.sync="dialogVisibleEdit" width="30%" @close="closeDialog">
        <el-form ref="editForm" :model="form" label-width="80px" :rules="rules">
          <el-form-item :label="title" prop="attr_name">
            <el-input v-model="form.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisibleEdit = false">取 消</el-button>
          <el-button type="primary" @click="editConfirm('editForm')">
            确 定
          </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 级联选择器
      options: [],
      props: {
        expandTrigger: 'hover',
        label: 'cat_name',
        children: 'children',
        value: 'cat_id'
      },
      value: [],
      // 标签页
      name: 'many',
      manyData: [],
      onlyData: [],
      inputVisible: false,
      inputValue: '',
      manyVal: [],
      onlyVal: [],
      /* 对话框 */
      dialogVisibleAdd: false,
      dialogVisibleEdit: false,
      form: {
        attr_name: '',
        attr_sel: '',
        attr_id: '',
        cat_id: ''
      },
      rules: {
        attr_name: [
          { required: true, message: '请输入参数名称', trigger: 'blur' },
          { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ]
      },
      disabled: true,
      /* 修改属性对话框 */
      title: '动态参数'
    }
  },
  methods: {
    /* 获取级联选择器数据信息 */
    async getOptions() {
      /* 获取分类数据  2级 */
      const res = await this.$axios.get('/categories', {
        params: {
          type: 3
        }
      })
      if (res.data.meta.status === 200) {
        this.options = res.data.data
      }
    },
    /* 级联选择器change事件 */
    handleChange(e) {
      if (e.length !== 3) {
        return this.$message({
          message: '只允许为第三级分类设置相关参数',
          type: 'error'
        })
      }
      this.upData()
    },
    /* 添加参数 */
    submitForm(formAdd) {
      this.$refs.formAdd.validate(async valid => {
        if (valid) {
          this.form.attr_sel = this.name
          const res = await this.$axios.post(`/categories/${this.value[2]}/attributes`, this.form)
          if (res.data.meta.status === 201) {
            this.$message({ message: res.data.meta.msg, type: 'success' })
            this.upData()
            this.dialogVisibleAdd = false
            this.form.attr_name = ''
          } else {
            this.$message({ message: res.data.meta.msg, type: 'error' })
          }
        } else {
          this.$message({ message: '参数名称不符合要求', type: 'error' })
        }
      })
    },

    /* 打开对话框 */
    edit(obj) {
      this.form = { ...obj.row }
      this.dialogVisibleEdit = true
    },
    editConfirm(editForm) {
      this.$refs.editForm.validate(async valid => {
        if (valid) {
          const res = await this.$axios.put(`/categories/${this.form.cat_id}/attributes/${this.form.attr_id}`, this.form)
          if (res.data.meta.status === 200) {
            this.$message({ message: res.data.meta.msg, type: 'success' })
            this.upData()
            /* 关闭界面 */
            this.dialogVisibleEdit = false
          } else {
            this.$message({ message: res.data.meta.msg, type: 'error' })
          }
        } else {
          this.$message({
            message: '您输入的' + this.title + '不符合规则',
            type: 'error'
          })
        }
      })
      // 更改attr_name
    },
    /* 对话框关闭时的回调 */
    closeDialog(done) {
      this.clearForm()
    },
    /* 清空form值数据 */
    clearForm() {
      Object.keys(this.form).map(key => {
        this.form[key] = ''
      })
    },
    del(obj) {
      this.$confirm('确认删除此条参数？')
        .then(async _ => {
          const res = await this.$axios.delete(`/categories/${obj.cat_id}/attributes/${obj.attr_id}`)
          if (res.data.meta.status === 200) {
            this.$message({ message: res.data.meta.msg, type: 'success' })
            this.upData()
          } else {
            this.$message({ message: res.data.meta.msg, type: 'error' })
          }
          console.log(res, 'delete')
        })
        .catch(_ => {})
    },
    /* 标签页切换 */
    tabClick(e) {
      // tab 切换
      this.name = e.name
      this.title = e.label
      if (this[this.name + 'Data'].length <= 0 && !this.disabled) {
        this.upData()
      }
    },
    /* tag 标签 */
    async handleClose(obj, i) {
      const attrVals = obj.row.attr_vals.split(',')
      attrVals.splice(i, 1)
      this[this.name + 'Data'][obj.$index].attr_vals = attrVals.join(',')
      const res = await this.$axios.put(`categories/${obj.row.cat_id}/attributes/${obj.row.attr_id}`, {
        attr_name: obj.row.attr_name,
        attr_vals: attrVals.join(','),
        attr_sel: this.name
      })
      if (res.data.meta.status === 200) {
        this.$message({ message: res.data.meta.msg, type: 'success' })
      } else {
        this.$message({ message: res.data.meta.msg, type: 'error' })
      }
    },
    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },
    async handleInputConfirm(obj) {
      if (this.inputValue.trim()) {
        if (obj.row?.attr_id) {
          // 返回数据列表
          const attrVals = obj.row.attr_vals ? obj.row.attr_vals.split(',') : []
          attrVals.push(this.inputValue)
          this[this.name + 'Data'][obj.$index].attr_vals = attrVals.join(',')
          const res = await this.$axios.put(`categories/${obj.row.cat_id}/attributes/${obj.row.attr_id}`, {
            attr_name: obj.row.attr_name,
            attr_vals: attrVals.join(','),
            attr_sel: this.name
          })
          if (res.data.meta.status === 200) {
            this.$message({ message: res.data.meta.msg, type: 'success' })
          } else {
            this.$message({ message: res.data.meta.msg, type: 'error' })
          }
        }
      }
      this.inputValue = ''
      this.inputVisible = false
    },
    async upData() {
      /* 获取商品参数 */
      const res = await this.$axios.get(`categories/${this.value[2]}/attributes`, {
        params: { sel: this.name }
      })
      if (res.data.meta.status === 200) {
        this[this.name + 'Data'] = res.data.data
      }
    }
  },
  created() {
    this.getOptions()
  },
  watch: {
    value: {
      handler(a) {
        if (a.length === 3) {
          this.disabled = false
        }
      },
      immediate: true
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  // margin-top: 15px;
  /deep/.cell {
    font-size: 12px;
  }
}
.el-tabs {
  margin-top: 15px;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.el-tag + .el-tag {
  margin-left: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>
